import React, { useState } from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import IconButton from "@mui/material/IconButton";
import AddIcon from "@mui/icons-material/Add";
import CloseIcon from "@mui/icons-material/Close";
import { ProductToolProps } from "../../interfaces/props";

const ProductTool: React.VFC<ProductToolProps> = (props) => {
    const [isHidden, setHidden] = useState(false);

    const { info } = props;

    const hide = () => {
        setHidden(true);
    };
    const show = () => {
        setHidden(false);
    };

    return (
        <Box
            sx={{
                position: "sticky",
                left: 10,
                bottom: 10,
            }}
        >
            <Paper
                elevation={6}
                color="primary"
                sx={{
                    height: isHidden ? 120 : 290,
                    width: "94vw",
                    maxWidth: 400,
                    textAlign: "center",
                    backgroundColor: "#ededed",
                }}
            >
                <IconButton
                    onClick={() => {
                        isHidden ? show() : hide();
                    }}
                    sx={{ flex: "1" }}
                    size="large"
                >
                    {isHidden ? (
                        <AddIcon sx={{ fontSize: 72 }} />
                    ) : (
                        <CloseIcon sx={{ fontSize: 48 }} />
                    )}
                </IconButton>
                <Box
                    sx={{
                        display: isHidden ? "none" : "flex",
                        flexDirection: "column",
                        justifyContent: "space-between",
                    }}
                >
                    <Typography variant="h3">{info.name} 님</Typography>
                    <Typography variant="h5">잔여포인트</Typography>
                    <Typography variant="h3" role="body1">
                        {info.points} pt
                    </Typography>
                </Box>
            </Paper>
        </Box>
    );
};

export default ProductTool;
